.contenido-servicios{
    text-align: center;
    h3{
        font-weight: $delgada;
        font-size: $separacion;
    }
}
.galeria-servicios {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    margin: auto;
    @include smartwatch{
        display: flex;
        flex-direction: column;
        .img-1{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/tratamiento-capilar-sevilla_hgfsfk.jpg');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            margin: auto;
            width: 20rem;
            height: 20rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 1.5rem;
                    margin: 0;
                }
                p{
                    color: $blanco;
                    font-size: 1rem;
                }
            }
    
        }
        .img-2{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/1-9-759x500_mbkf5i.jpg');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            align-items: center;
            margin: auto;
            width: 20rem;
            height: 20rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 1.5rem;
                    margin: 0;
                }
                p{
                    color: $blanco;
                    font-size: 1rem;
                }
            }
        }
        .img-3{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/como_poner_precios_en_una_peluqueria_48499_600_d4el6b.jpg');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            align-items: center;
            margin: auto;
            width: 20rem;
            height: 20rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 1.5rem;
                    margin: 0;
                }
                p{
                    color: $blanco;
                    font-size: 1rem;
                }
            }
        }
        .img-4{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785866/Manicure-y-pedicure-1_p57fvf.png');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            align-items: center;
            margin: auto;
            width: 20rem;
            height: 20rem;
            border-radius: 50%;
        }
        .div-size{
            justify-content: center;
            align-items: center;
            margin: 2.5rem;
            
            h5{
                font-weight: $delgada;
                font-size: 1.5rem;
                margin: 0;
            }
            p{
                margin-bottom: 0;
                color: $blanco;
                font-size: 1rem;
            }
        }
    }
    @include telefono{
        display: flex;
        flex-direction: column;
        .img-1{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/tratamiento-capilar-sevilla_hgfsfk.jpg');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            margin: auto;
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2.5rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 2rem;
                    margin: 0;
                }
                p{
                    margin-bottom: 0;
                    color: $blanco;
                    font-size: 1.5rem;
                }
            }
    
        }
        .img-2{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/1-9-759x500_mbkf5i.jpg');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            align-items: center;
            margin: auto;
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2.5rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 2rem;
                    margin: 0;
                }
                p{
                    margin-bottom: 0;
                    color: $blanco;
                    font-size: 1.5rem;
                }
            }
        }
        .img-3{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/como_poner_precios_en_una_peluqueria_48499_600_d4el6b.jpg');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            align-items: center;
            margin: auto;
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2.5rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 2rem;
                    margin: 0;
                }
                p{
                    margin-bottom: 0;
                    color: $blanco;
                    font-size: 1.5rem;
                }
            }
        }
        .img-4{
            background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785866/Manicure-y-pedicure-1_p57fvf.png');
            background-repeat: no-repeat;
            border-radius: 50%;
            background-size: cover;
            align-items: center;
            margin: auto;
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
            .div-size{
                justify-content: center;
                align-items: center;
                margin: 2.5rem;
                
                h5{
                    font-weight: $delgada;
                    font-size: 2rem;
                    margin: 0;
                }
                p{
                    margin-bottom: 0;
                    color: $blanco;
                    font-size: 1.5rem;
                }
            }
        }
    
    }

  }
